import { Component, OnInit, Output, EventEmitter, Input, ViewEncapsulation } from '@angular/core';
import {MatDialog, MatDialogRef} from '@angular/material/dialog';
import { noop } from 'rxjs';
import { title } from 'process';


@Component({
  selector: 'app-confirmation-dialog',
  template: '',
  encapsulation: ViewEncapsulation.None
})
export class ConfirmationDialogComponent implements OnInit {
  @Input() title: any = 'Delete';
  @Input() content: any = 'Are you sure you want to delete the Record?';
  @Output() emitResult = new EventEmitter<any>();
  constructor( public dialog: MatDialog ) { }

  ngOnInit(): void {
    const dialogRef = this.dialog.open(DeleteDialogComponent, {
      width: '400px',
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {      
      result ?  this.emitResult.emit(true) : this.emitResult.emit();
    });
    const a  = DeleteDialogComponent;
    a.prototype.title = this.title;
    a.prototype.content = this.content;
  }

}

@Component({
  selector: 'app-delete-contact-dialog',
  templateUrl: 'confirmation-dialog.component.html',
})
export class DeleteDialogComponent {
  title: any;
  content: any;
  emitResult = new EventEmitter<any>();
  constructor(
    public confirmDialogRef: MatDialogRef<DeleteDialogComponent>) {
  }

  onCancel(): void {
    this.confirmDialogRef.close();
  }

  onConfirm() {
    this.confirmDialogRef.close(true);
  }

}