import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProcessService } from '../process.service';
import { CommonService } from '../../../common.service';
let currentCategory: any = null;

@Component({
  selector: 'app-all-process-view',
  templateUrl: './all-process-view.component.html',
  styleUrls: ['./all-process-view.component.css']
})
export class AllProcessViewComponent implements OnInit {
  baseUrl = "https://api.myairliftusa.com/uploads/"
  sideNav = [
    { name: 'all', display_value: 'All', icon: 'view_list' },
    { name: 'process', display_value: 'Process', icon: 'mediation' },
    { name: 'masters', display_value: 'Masters', icon: 'leaderboard' },
    { name: 'others', display_value: 'Others', icon: 'folder' },
  ];
  groupBy = 'all';
  groupList = [];
  selectedNav = 'all';
  data = {}
  inputValue = ''
  dataGroup = [];
  dialogRef: any;
  groupInfo: any;
  groupProcess: any = [];
  currentGroup: any;
  searchProcsss: any = '';
  processLoader: any;
  noProcessData: any;
  constructor(private route: Router, public dialog: MatDialog, private processService: ProcessService, public commonService: CommonService) {
    this.getGroup();
  }

  ngOnInit(): void {
  }

  redirectDashboard() {
    currentCategory = null;
    this.route.navigate(['dashboard']);
  }

  getGroup() {
    const data: any = {};
    if (this.selectedNav !== 'all') {
      data.type = 'filter';
      data.value = this.selectedNav;
    }

    this.currentGroup = [];
    this.processService.getGroups('', data).subscribe((response: any) => {
      this.dataGroup = response['info'];
      if (this.dataGroup && this.dataGroup[0]) {
        this.currentGroup = currentCategory?.id ? currentCategory : this.dataGroup[0];
        this.getProcess(this.currentGroup?.id, 'group_id');
      } else {
        this.groupProcess = [];
      }
    });
  }

  getProcess(value, type) {
    const filter: any = {};
    if (this.currentGroup?.id) {
      filter.group_id = this.currentGroup?.id;
    }
    // if (this.selectedNav !== 'all')
    //   filter.process_type = this.selectedNav;

    this.groupProcess = [];
    this.processLoader = true;
    this.noProcessData = false;
    this.processService.getProcess(value, type, filter).subscribe((response: any) => {
      this.processLoader = false;
      this.groupProcess = response['info'];
      this.noProcessData = this.groupProcess?.length ? false : true;
    });
  }

  nullCurrent() {
    currentCategory = null;
  }

  createDialog(templateName): void {
    this.dialogRef = this.dialog.open(templateName, {
      width: '500px',
    });
    this.dialogRef.afterClosed().subscribe(result => {
      console.log(result);
      this.onsubmit(templateName, result);
    });
  }

  onsubmit(template, result) {
    console.log(template, 'rtempla');

    if (template?._declarationTContainer?.localNames[0] === 'newCategory' && result) {
      this.processService.createGroup(result).subscribe((response: any) => {
        if (response?.statusCode === 200) {
          this.commonService.showSnackbar('Successfully Category Created', ['green-growl']);
        } else {
          this.commonService.showSnackbar('Category Not Created, please Try Again', ['red-growl']);
        }
        const data: any = {};
        if (this.selectedNav !== 'all') {
          data.type = 'filter';
          data.value = this.selectedNav;
        }
        this.processService.getGroups('', data).subscribe((response: any) => {
          this.dataGroup = response['info'];
        });
      }, (error: any) => {
        this.commonService.showSnackbar('Something Went Wrong, Please Try After SomeTime', ['red-growl']);
      });
    } else if (template?._declarationTContainer?.localNames[0] === 'newProcessTemplate' && result) {
      result.table_name = this.processService.convertSnackCase(result?.display_name);
      result.icon = result?.icon ? result.icon : '3d_rotation';
      this.processService.createProcess(result).subscribe((response: any) => {
        if (response?.statusCode === 200) {
          this.commonService.showSnackbar('Successfully Process Created', ['green-growl']);
          const data: any = JSON.parse(localStorage.getItem('user_data'));
          if (data && data.designation) {
            this.processService.getDesignation(parseInt(data.designation)).subscribe((data: any)=> {
              if (data.statusCode === 200 && data['info'] && data['info'][0]) {
                console.log('am response');
                
                const payload: any = {
                  "values": [
                      {
                          "designation_id": data['info'][0].id,
                          "designation": data['info'][0].designation,
                          "table_id": response.last_insert_id[0],
                          "create_item": 1,
                          "edit_process": 1,
                          "deletes": 1,
                          "updates": 1,
                          "reports": 1,
                          "mail": 1,
                          "mass_update": 1,
                          "mass_mail": 1
                      }
                  ],
                  "table_name": "process_permission"
                };
                this.commonService.commonInsert(payload).subscribe((response: any) => {
                  if (response?.statusCode === 200) {              
                  } else {
                    console.log('permission not inserted');
                  }
                }, (error: any) => {
                  console.log('permission api error');
                });
              }
              
            });
          }          
        } else {
          this.commonService.showSnackbar('Process Not Created, please Try Again', ['red-growl']);
        }
        this.creteColuns(result.table_name, response.last_insert_id[0])
        this.getProcess(this.currentGroup?.id, 'group_id');
      }, (error: any) => {
        this.commonService.showSnackbar('Something Went Wrong, Please Try After SomeTime', ['red-growl']);
      });
    }
  }

  creteColuns(table, id) {
    let value = { table_name: table, user_id: '2', table_id: id }
    this.commonService.createValues(value, "table_columns_ordering").subscribe((response: any) => {
    });
  }

  createCategory(value) {
    console.log(value, 'value');
  }

  selectedGroup(group) {
    this.currentGroup = group;
    currentCategory = this.currentGroup;
    this.getProcess(this.currentGroup?.id,'group_id');
  }


}
