import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { iconList } from '../materialIcons';
import { ProcessService } from '../process.service';

@Component({
  selector: 'app-create-process',
  templateUrl: './create-process.component.html',
  styleUrls: ['./create-process.component.css']
})
export class CreateProcessComponent implements OnInit {
  @Input() process_type = new EventEmitter<any>();
  @Input() group = new EventEmitter<any>();
  @Output() closeEvent = new EventEmitter();
  filterIconList = [];
  materialIconList = [];
  iconList = iconList;
  processForm: FormGroup;
  selectGroup: any;
  processType: any;
  groupList: any;
  groupListCopy: any;
  nameError: any;
  processName: any;
  groupData: any;
  process:any = '';

  constructor(public fb: FormBuilder, public processService: ProcessService) {
    this.iconList.categories.forEach(i => { i.icons.forEach(j => { this.materialIconList.push(j.id); }); });
    this.filterIconList = this.materialIconList;
  }

  ngOnInit(): void {     
    this.groupData = this.group;
    this.getGroups(this.process_type);
    this.processForm = this.fb.group({
      display_name: ['', Validators.required],
      description: [''],
      show_on_side_nav: ['no'],
      icon: [''],
      icon_img_url: [''],
      group_name: [this.groupData?.group_name, Validators.required],
      group_id: [''],
      process_type: [this.process_type, Validators.required]
    });
    this.selectedGroup(this.groupData);
  }

  getGroups(process) {
    const data: any = {
      // type: 'filter',
      // value: this.process_type
    };
    if (process && process !== 'all') {
      data.type = 'filter';
      data.value = process;
    }
    this.processService.getGroups('', data).subscribe((response: any) => {
      this.groupList = response['info'];
      this.groupListCopy = this.groupList;
    });
  }

  iconsFilter(val) {
    this.filterIconList = this.materialIconList.filter(i => i.includes(val))
  }

  selectedGroup(group) {
    this.selectGroup = group;
    this.processForm.get('group_id').setValue(this.selectGroup.id);
  }

  checkTableExist(value) {
    if (value && value.length) {
      this.processService.checkTable(this.processService.convertSnackCase(value)).subscribe((response: any) => {
        this.nameError = (response && response['info'] && response['info'].length) ? true : false;
        if (this.nameError) {
          const temp: any = this.processForm.get('display_name').value;
          this.processForm.get('display_name').setValue('');
          this.processName = temp;
        }
      });
    }
  }

  onProcessType(value) {
    this.getGroups(value);
  }

  groupFilter(val) {
    this.groupList = this.groupListCopy.filter(i => i['group_name'].toLowerCase().includes(val.toLowerCase()))
  }

}
