import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { noop } from 'rxjs';
import { CommonService } from 'src/app/common.service';
import { ProcessService } from '../process.service';

@Component({
  selector: 'app-single-process-card',
  templateUrl: './single-process-card.component.html',
  styleUrls: ['./single-process-card.component.css']
})
export class SingleProcessCardComponent implements OnInit {

  @Input() inputData: any;
  @Output() onDelete = new EventEmitter<any>();
  baseUrl = "https://api.myairliftusa.com/uploads/"
  isClicked: boolean = false;
  menuOpened = false;
  viewConfirmation = false;

  constructor(public processService: ProcessService, public router: Router, public commonService: CommonService) { }

  ngOnInit(): void {
  }

  emitConfirmation(event) {
    this.viewConfirmation = false;
    event ? this.deleteProcess() : noop();
  }
  deleteProcess() {
    this.processService.deleteProcess(this.inputData?.id).subscribe((response: any) => {
      if (response['statusCode'] === 200) {
        this.onDelete.emit();
      }
    });
  }

  openDelete() {
    this.viewConfirmation;
    setTimeout(() => {
      this.viewConfirmation = true;
    }, 100);
  }

  goToProcess() {
    this.inputData["process"] = "view-all";
    this.router.navigate(['/edit-process', this.inputData?.table_name], { queryParams: this.inputData,});
  }

  goToDashboard() {
    let temp = { id: this.inputData.id, tablename: this.inputData.table_name, }

    this.router.navigate(['/dashboard/view', this.inputData.table_name], { queryParams: temp, });
  }

  updateSide(value) {
    this.inputData.show_on_side_nav = value;
    this.commonService.putColumns(this.inputData, 'meta_table_data').subscribe((response: any) => {
      
    });
  }
}
