import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { CommonService } from '../../common.service';

@Injectable({
  providedIn: 'root'
})
export class ProcessService {
  baseUrl = environment.url;

  //Format date for httpOption
  formatDate() {
    let d = new Date(),
      formatDateVal = [this.formatNumber(d.getFullYear()), this.formatNumber(('' + (d.getMonth() + 1))), this.formatNumber(('' + d.getDate()))],
      formatTimeval = [this.formatNumber(('' + d.getHours())), this.formatNumber(('' + d.getMinutes())), this.formatNumber(('' + d.getSeconds()))];
    return formatDateVal.join('-') + ' ' + formatTimeval.join('-');
  }

  //add "0" before number if length 1
  formatNumber(value: any) {
    if (value.length < 2) {
      return value = '0' + value;
    } else {
      return value;
    }
  }

  constructor(private http: HttpClient, private commonService: CommonService) { }

  getGroups(value, data) {
    const payload: any = {
      "limit": 100,
      "page": 0,
      "sort_type": "ASC",
      "sort_field": "id",
      "search": [
        {
          "group_name": value
        }
      ]
    };
    if (data.type === 'filter') {
      payload.filter = [
        { 'process_type': data.value }
      ]
    }
    return this.http.post(this.baseUrl + 'getProcessGroup/', payload,this.commonService.httpOptions);
  }

  createGroup(value) {
    const payload: any = {
      "values": [
          value
      ]
    };
    return this.http.post(this.baseUrl + 'createProcessGroup/', payload, this.commonService.httpOptions);
  }

  putGroup() {
    const payload: any = {
      "values": [
          {
            "group_name": "Admin",
              "id": 1
          }
      ]
    };
    return this.http.put(this.baseUrl + 'putProcessGroup/', payload, this.commonService.httpOptions);
  }

  getProcess(value, type, filter?:any) {
    const payload: any = {
      "limit": 100,
      "page": 0,
      "sort_type": "ASC",
      "sort_field": "id",
    };

    if (filter?.sort_type)
      payload.sort_type = filter.sort_type;
    
    if (filter?.sort_field)
      payload.sort_field = filter.sort_field;

    payload.filter = [];
    if (filter && filter?.group_id) {
      payload.filter.push( { 'group_id': filter.group_id } );
    }
    console.log(filter, 'filter');
    
    if (filter && filter?.process_type) {
      payload.filter.push({ 'process_type': filter.process_type });
    }

    if (type === 'table_name') {
      payload.search = [
        {
          "table_name": value,
        }
      ]
    } else if (type === 'group_id' ){
      payload.search = [
        {
            "group_id": value,
        }
      ]
    } else if (type === 'id') {
      payload.search = [
        {
            "id": value,
        }
      ]
    }
    return this.http.post(this.baseUrl + 'getProcess/', payload, this.commonService.httpOptions);
  }

  createProcess(value) {
    const payload: any = {
      "values": [
          value
      ]
    };
    return this.http.post(this.baseUrl + 'createProcess/', payload, this.commonService.httpOptions);
  }

  putProcess()  {
    const payload: any = {
      "values": [
          {
            "table_name": "test",
            "id": 11
          }
      ]
    };
    return this.http.put(this.baseUrl + 'putProcess/', payload, this.commonService.httpOptions);
  }

  deleteProcess(id) {
    return this.http.delete(this.baseUrl + 'deleteProcess/' + id, this.commonService.httpOptions);
  }

  checkTable(value) {
    const payload:any = {
      "table_name": value
    }
    return this.http.post(this.baseUrl + 'checkTable/', payload, this.commonService.httpOptions);
  }

  convertSnackCase(value) {
    return value && value.match(/[A-Z]{2,}(?=[A-Z][a-z]+[0-9]*|\b)|[A-Z]?[a-z]+[0-9]*|[A-Z]|[0-9]+/g)
    .map(x => x.toLowerCase())
    .join('_');
  }

  getDesignation(id) {
    const payload: any = {
      "limit": 100,
      "page": 0,
      "db_name": "tracking_tb",
      "table_name": "designation",
      "sort_type": "ASC",
      "sort_field": "id",
      "filter": [
        {
          "id": id
        }
      ]
    };
    return this.http.post(this.baseUrl + 'commonGet/', payload, this.commonService.httpOptions)
  }

}
